@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  background-color: #1d1d25;
  color: #f9f9f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  --bg-dark: #1d1d25;
  --bg-light: #f9f9f9;
}
.header{position:fixed;top:0;left:0;width:100%;min-height:80px;z-index:9;background:#1e1e26;box-shadow:rgba(0,0,0,0.1) 0 0 20px}


.footer{padding:40px 30px;text-align:center;margin-top:30px;background:url(https://gramotech.com/html/tigers/images/twitterbg.jpg) no-repeat center top;-webkit-filter:saturate(0.5);filter:saturate(0.5)}

.cards{display:flex;flex-wrap:wrap}.card{position:relative;width:100%;background-color:#fff;background-color:#403e4e;background-color:#f1f1f1;overflow:hidden;margin:10px 10px 10px;height:200px}.card__name{z-index:1;font-family:"Poppins", sans-serif;position:absolute;bottom:0;left:10px;font-size:30px;font-weight:700;color:#fff;margin:10px 0;display:block;width:calc(100% - 90px);overflow:hidden;white-space:nowrap;text-overflow:ellipsis;pointer-events:none;text-shadow:rgba(0,0,0,0.5) 2px 2px 10px}@media (min-width: 992px){.card__name{font-size:50px}}.card__logo{position:absolute;top:0;right:0;width:80px;padding:15px 20px;z-index:1;-webkit-filter:drop-shadow(0px 0px 2px #000);filter:drop-shadow(0px 0px 2px #000)}.card__trigger{position:absolute;cursor:pointer;border:0;background:transparent;transition:all 0.2s ease;color:#fff;width:100%;right:0;bottom:0;height:100%;text-align:right;padding-right:25px;z-index:2;transform:scale(1);opacity:1}@media (min-width: 992px){.card__trigger{transform:scale(0.1);opacity:0;bottom:20px;height:auto}}.card__trigger i{font-size:30px}@media (min-width: 600px){.card__trigger i{font-size:40px}}.card__trigger:focus{outline:0}.card__material{z-index:1;position:absolute;top:20px;right:70px;color:#333;opacity:0.7;font-size:12px;letter-spacing:1px;-webkit-writing-mode:vertical-lr;-ms-writing-mode:tb-lr;writing-mode:vertical-lr;transition:all 0.2s ease}.card__material-logo{position:absolute;width:50px;right:10px;top:10px;z-index:1}.card__pic{z-index:100;width:calc(100% - 80px);height:200px;background-size:cover;background-position:center;box-shadow:#000 -10px 0 100px inset}@media (min-width: 768px){.card__pic{background-size:100%;-webkit-filter:grayscale(100%);filter:grayscale(100%)}}.card__social{position:absolute;left:60px;top:0;display:flex;justify-content:space-around;align-items:center;width:180px;height:64px;border-radius:80px;z-index:1}.card__social a{opacity:0;will-change:transform;transform:scale(0.1);display:inline-block;width:30px;height:30px;background:#000;text-align:center;font-size:18px;color:#fff;border-radius:20px;line-height:30px;transition:all 0.2s ease}.card__social a:nth-of-type(1){transition-delay:0.4s}.card__social a:nth-of-type(2){transition-delay:0.3s}.card__social a:nth-of-type(3){transition-delay:0.2s}.card__social a:nth-of-type(4){transition-delay:0.1s}.card:hover i{opacity:1;transform:scale(1)}.card__button{position:absolute;right:24px;bottom:14px;width:30px;height:30px;background-color:#000;border:none;border-radius:30px;cursor:pointer;outline:none;transition:all 0.3s ease;mix-blend-mode:multiply}.card__button i{font-size:3rem}@media (min-width: 992px){.card:hover .card__button{transform:scale(13)}}@media (min-width: 992px) and (min-width: 600px){.card:hover .card__button{transform:scale(16.5)}}@media (min-width: 992px){.card:hover .card__material{color:#fff}.card:hover .card__material-logo{-webkit-filter:invert(1);filter:invert(1)}.card:hover .card__pic{-webkit-filter:grayscale(50%);filter:grayscale(50%)}.card:hover .card__trigger{transform:scale(1);opacity:1}.card:hover .card__social a{transform:scale(1);opacity:1}}@media (min-width: 992px){.card{width:calc(50% - 30px)}}

.details{background:#fff;color:#000;z-index:100;width:100%;overflow:scroll;overflow-x:hidden;border-radius:0;position:fixed;max-width:1170px;top:50%;left:50%;opacity:0;transition:opacity 0.25s ease-in-out, transform 1.15s ease-in-out;transform:translate(-50%, 150%);overflow:scroll;height:100vh}.details.show{opacity:1;transform:translate(-50%, -50%);display:block}@media (min-width: 992px){.details{border-radius:20px;overflow:hidden;height:auto;min-height:350px}}.details__brands{margin-left:0}@media (min-width: 992px){.details__brands{margin-left:-15px}}.details__infos{margin-top:50px}@media (min-width: 992px){.details__infos{margin-top:0}}.details__calltoaction{margin-bottom:40px}.details__shirts{margin-bottom:40px}.details__close{position:absolute;background:rgba(255,255,255,0.5);z-index:1;border:0;padding:0;top:20px;left:20px;font-size:25px;border-radius:50px;width:50px;height:50px;color:#000;cursor:pointer}.details__effects{display:none}@media (min-width: 992px){.details__effects{display:block}}.details__effects .effect__polygon{background:#000;position:fixed;top:0;display:block;height:100%;z-index:99;transition:all 2s ease-in-out}.details__effects .effect__polygon--one{left:-20%;width:0;opacity:0.9;transform:skewX(-20deg);z-index:100}.details__effects .effect__polygon--one.show{width:60%}.details__effects .effect__polygon--two{background:#fff;width:0;right:0;z-index:99;opacity:0.9}.details__effects .effect__polygon--two.show{width:100%;transition:all 0.85s ease-in-out}.shirts-list__box{background:#fff;box-shadow:rgba(0,0,0,0.4) 0 4px 25px -10px;border-radius:10px;margin:0 40px 0 0;display:block;position:relative;transition:all 100ms ease-in-out;cursor:pointer;width:calc(50% - 40px);margin:0 20px 30px;text-align:center}.shirts-list__box.active{box-shadow:rgba(0,0,0,0.4) 0 4px 35px -10px}.shirts-list__box.active .shirts-list__image{transform:scale(1) translateY(-20px)}@media (min-width: 992px){.shirts-list__box{width:calc(33% - 40px)}.shirts-list__box:hover .shirts-list__image{transform:scale(1) translateY(-10px)}}.shirts-list__image{transform:scale(0.9);transition:all 100ms ease-in-out}.shirt-info{line-height:35px;margin-left:15px}.principal-shirt{margin:0 -15px}.principal-shirt__background{position:absolute;left:0;top:0;width:100%;height:100%;-webkit-filter:grayscale(100%);filter:grayscale(100%);mix-blend-mode:multiply;background-position:center;background-size:cover}.principal-shirt__background.opacity{mix-blend-mode:inherit;opacity:0.4}.principal-shirt__image{transform:translate(0, 20%);width:370px;z-index:1;position:relative;margin:0 auto;display:block}@media (min-width: 992px){.principal-shirt__image{position:absolute;right:0;top:50%;transform:translate(50%, -50%);width:430px}}@media (min-width: 992px){.principal-shirt{width:calc(100% + 15px);height:100%;position:absolute}}.datail-logos{position:absolute;z-index:1}

